export default {
	data() {
		return {
			windowW:'',
			banner:{
				indicatorDots: true,
				circular: true,
				indicatorColor: "rgb(221,221,221)",
				indicatorActiveColor: "rgba(201,0,10,1)",
				autoplay: false,
				interval: 2000,
				duration: 500,
				previousMargin:'',
				nextMargin:'',
			}
		}
	},
	created(){
		var _this=this;
	},
	mounted(){
		var _this=this;
		uni.getSystemInfo({
			success: function (res) {
				_this.windowW=res.windowWidth;
				_this.banner.previousMargin=_this.windowW*30/375;
				_this.banner.nextMargin=_this.windowW*20/375;
			}
		});
	},
	methods: {
		supermarket() {
			uni.navigateTo({
			    url: 'supermarket/supermarket',
			});
		},
	}
}
